@import "main";

:root {
    font-family: Verdana;
    text-de: en-US;
    margin: 0 0 0 0;
    text-align: center;
    --dark: #0040ff;
    --light: #ffbf00;
    --tert: #003958;
    --shadow: #888888;
    --p-light: #FFFDD0;
    font-size: 12px;
}

nav {
  display: flex;
  position: fixed;
  flex-direction: row;
  background-image: linear-gradient(to right, var(--dark), #00b365);
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  width: 100vw;
  margin-top: 0px;
  font-weight: 900;
  z-index: 1000;
  top: 0px;
  left: 0px;
  justify-content: flex-end;
}

.navlink {
  color: var(--dark);
  padding: 1rem;
  text-decoration: none;
  margin-top: 0px
}

a {
  text-decoration: none;
}

.welcome-header {
    font-size: 10rem;
}

.welcome-subheader {
    padding-bottom: 4rem;
    font-size: 2rem;
}

.section-header {
    font-size: 3rem;
    color: var(--light);
    padding-bottom: 4rem;
}

.s-circle {
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
    background: var(--dark);
    position: absolute;
    right: 0px;
    z-index: 999;
}

.m-circle {
    height: 12rem;
    width: 12rem;
    border-radius: 50%;
    background: var(--light);
    position: absolute;
    right: 0px;
    z-index: 1000;
}

.circle-box {
    display: flex;
    justify-content: space-around;
}

#main-doc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.project-tile {
  /*box-shadow: 2px 2px 2px 2px var(--p-light);*/
  background-color: var(--light);
  color: var(--dark);
  padding-left: 2rem;
  padding-right: 2rem;
  justify-content: center;
  justify-items: center;
  align-items: center;
  width: 19rem;
  height: 19rem;
/*  border-radius: 50%;*/
}

.project-tile:hover:active, .scroll:hover:active {
  background-color: var(--p-light);
  color: var(--light);
}

.project-tile-title {
    font-size: 2rem;
    padding-top: 2rem;
}

@media (max-width: 600px) {
  html {
    width: 100vw;
    height: 100vh;
    font-size: 0.5rem;
  }
}

.show-grid {
  display: grid;
  justify-items: center;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-template-rows: repeat(auto-fit); /*I added this to check if it solves the mobile issue*/
  grid-gap: 4rem;
  width: 100%;
  margin: 0 0;
  margin-bottom: 6rem;
}

.scroll {
  background-color: var(--light);
  color: var(--dark);
  /*box-shadow: 0px 2px 2px 0px var(--p-light);*/
  border-style: none;
  padding: 1rem;
}

/*direct child selector*/
section > a {
      font-size: 1.5rem;
    }

.scroll:hover {
 color: var(--light);
}

.screen {
  min-height: calc(100vh - 12rem);
  padding-top: 8rem;
  padding-bottom: 4rem;
  left: 0px;
  right: 0px;
  background-color: var(--p-light);
  width: 100vw;
  color: var(--light);
  align-items: center;
/*  overflow: auto;*/
}

/*@media (max-width: 600px) {
  .screen {
    width: 100vw;
    height: auto;
    font-size: 0.5rem;
  }
}*/


#proj {
  background-image: linear-gradient(to right, var(--dark), #00b365);
  color: var(--dark);
}

footer {
  background-image: linear-gradient(to right, var(--dark), #00b365);
  color: var(--dark);
  display: flex;
  position: fixed;
  width: 100vw;
  margin-top: 0px;
  font-weight: 900;
  z-index: 1000;
  bottom: 0px;
  left: 0px;
  justify-content: center;
}

.light-text {
    color: var(--light);
}

.bar-50 {
    height: 1rem;
    width: 5rem;
    background-color: #ffbf00;
}

.bar-85 {
    height: 1rem;
    width: 8.5rem;
    background-color: #ffbf00;
}

.bar-90 {
    height: 1rem;
    width: 9rem;
    background-color: #ffbf00;
}

.progress {
    display: grid;
    justify-items: center;
    align-items: center;
    grid-gap: 2rem;
    width: 50%;
}